.btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  max-width: 100%;
  padding: 0.8rem 1.2rem;
  background-color: #1A8C33;
  font-family: "Higure Gothic";
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 100%;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  border-radius: 90px;
  cursor: pointer;
  z-index: 0;
}

.btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  border-radius: inherit;
  border: 2px solid #1A8C33;
  z-index: 2;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transform-origin: right top;
  transform: scale(0, 1);
  z-index: 1;
}

.btn:hover {
  color: #1A8C33;
}

.btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.btn span {
  position: relative;
  color: inherit;
  font-weight: inherit;
  text-decoration: none;
  z-index: 3;
}

@media only screen and (max-width: 768px) {
  .btn {
    padding: 1.3rem 6.0rem 1.3rem 3.2rem;
    font-size: 1.6rem;
    line-height: 2.4rem;
    letter-spacing: 0.1em;
    border-radius: 2.5rem;
  }
  .btn::after {
    border-width: 2px;
  }
}

