.news-list {
  position: relative;
  width: 100%;
}

.news-list .news-item {
  display: flex;
  column-gap: 3.2rem;
  align-items: center;
  height: 6.0rem;
  border-bottom: 1px solid #EAEAEA;
  background: #FFF;
}

.news-list .news-item:hover {
  cursor: pointer;
}

.news-list .news-item:hover .date {
  opacity: 0.7;
}

.news-list .news-item:hover .title {
  opacity: 0.7;
}

.news-list .news-item:hover svg path {
  fill: #1A8C33;
}

.news-list .news-item .date {
  width: 6.7%;
  white-space: nowrap;
  flex-shrink: 0;
  color: #888;
}

.news-list .news-item .title {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.news-list .news-item svg {
  width: 1.6rem;
  height: 1.6rem;
  flex-shrink: 0;
}

.news-list .news-item svg path {
  fill: #C7C7C7;
}

.href-right {
  display: flex;
  column-gap: 1.6rem;
  align-items: center;
}

.href-right:hover {
  cursor: pointer;
  opacity: 0.7;
}

.href-right .svg-wrapper {
  position: relative;
  width: 4.0rem;
  height: 4.0rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 90px;
  background: #1A8C33;
  flex-shrink: 0;
}

.href-right .svg-wrapper svg {
  width: 1.6rem;
  height: 1.6rem;
}

.report-list {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(0px, 1fr));
  column-gap: 4.0rem;
  row-gap: 4.6rem;
}

.report-list .report-item {
  width: 100%;
  display: block;
}

.report-list .report-item img {
  width: 100%;
  aspect-ratio: 373 / 272;
  flex-shrink: 0;
  margin-bottom: 2.0rem;
  border-radius: 16px;
}

.report-list .report-item .title {
